import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getExperiences } from '../actions/experiences';
import { Link } from 'react-router-dom';
import Loader from './Loading';

const mapStateToProps = state => ({
    ...state.experience
});

const cardStyle = {
    MarginTop: '30px',
    MarginBottom: '30px'
};

class Experiences extends Component {

    componentDidMount(){
        this.props.dispatch(getExperiences());
    }
    
    render() {
        if(this.props.payload) {
            return (
                <div className="container experiences-container">
                    <div className="row">
                        <div className="col-md-12 text-center">
                            <h1 className="experiences-title font-weight-bold">University Honors Experiences</h1>
                        </div>
                    </div>
                    <div className="row">
                        <div className="card-deck">
                            {this.props.payload.map((item, index) => (
                                <div className="col-sm-6">
                                    <div className="card experiences-card">
                                        <img className="card-img-top" height="150px" src={item.acf.header_image} alt="Image for Honors Experience" />
                                        <div className="card-body text-center">
                                            <h4 class="card-title">{item.title.rendered}</h4>
                                            <Link className="btn btn-primary" to={`/honors/${item.id}`}>View Experience</Link>
                                        </div>
                                        <br/>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
    
                </div>
            );
        }
        return (
            <Loader />
        );
    }


}

export default connect(mapStateToProps)(Experiences);



