import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getExperience } from '../actions/experiences';
import {Link} from 'react-router-dom';
import Loader from './Loading';

const mapStateToProps = state => ({
    ...state.experience
});

class Honor extends Component {


    renderFile(file) {
        if (file) {

            let name = file.substring(file.lastIndexOf("/") + 1)

            return(
                <div className="card">
                    <div className="card-body">
                        <a href={file} target="_blank">{name}</a>
                    </div>
                </div>
            );
        }
    }

    componentDidMount(){
        this.props.dispatch(getExperience(this.props.match.params.id));
    }
    
    render() {
        if (this.props.title) {
            return (
                <div>
                    <div className="jumbotron jumbotron-fluid jumbotron-experience d-flex align-items-center" style={{backgroundImage: "url(" + this.props.header_image + " )"}}>
                        <div className="container text-center">
                            <h1 className="font-weight-bold">{this.props.title}</h1>
                        </div>
                    </div>
                    <div className="container experience-downloads-container">
                        <div className="experience-content" dangerouslySetInnerHTML={{__html: this.props.content}} />
                        <h3 className="downloads-heading">Downloads</h3>
                        {this.renderFile(this.props.file)}
                        {this.renderFile(this.props.file_extra)}
                    </div>
                </div>
            );
        }

        return (
            <Loader />
        );
        
    }


}

export default connect(mapStateToProps)(Honor);



