//Actions for experiences
import axios from 'axios';
import { GET_EXPS, GET_EXP } from './types.js';

const API_URL = 'http://beta.kevinpthomas.me/wp-json/wp/v2';

//function to get all experiences
export function getExperiences() {
    return function (dispatch) {
        axios.get(`${API_URL}/experiences`)
        .then((response) => {
            dispatch({
                type: GET_EXPS,
                payload: response.data
            });
        })
        .catch((error) => {
            console.log(error.response);
        });
    };
}

export function getExperience(id) {
    return function (dispatch) {
        axios.get(`${API_URL}/experiences/${id}`)
        .then((response) => {
            dispatch({
                type: GET_EXP,
                payload: response.data
            });
        })
        .catch((error) => {
            console.log(error.response);
        });
    };

}


