import { GET_EXPS, GET_EXP } from '../actions/types';
import { LOCATION_CHANGE } from 'react-router-redux';

const INITIAL_STATE = { error: '', id: '', title: '', content: '', header_image: '', file: '', file2: '' , payload: ''};

export default function (state = INITIAL_STATE, action) {
    switch (action.type) {
        case GET_EXPS:
            return { ...state,
                payload: action.payload
            };
        case GET_EXP:
            return { ...state,
                error: '',
                id: action.payload.id,
                title: action.payload.title.rendered,
                content: action.payload.content.rendered,
                header_image: action.payload.acf.header_image,
                file: action.payload.acf.file,
                file2: action.payload.acf.file_extra };
    }

    return state;
}


