import React from 'react';
import { Link } from 'react-router-dom';

function Footer() {
  return (
    <footer id="main-footer">
        <div className="container">
            <div className="row footer-element">
                <div className="col-md-12 text-center">
                    <a href="https://twitter.com/kevin_thomas12" target="_blank"><i className="fa fa-twitter fa-lg social-icon" aria-hidden="true"></i></a>
                    <a href="https://github.com/kevthomas10" target="_blank"><i className="fa fa-github fa-lg social-icon" aria-hidden="true"></i></a>
                    <a href="https://www.linkedin.com/in/kevinpaulthomas/" target="_blank"><i className="fa fa-linkedin fa-lg social-icon" aria-hidden="true"></i></a>
                </div>
            </div>

            <div className="row footer-element">
                <div className="col-md-12 text-center">
                    &copy; 2017 Kevin Thomas. All Rights Reserved.
                </div>
            </div>            
        </div>
    </footer>
  );
}

export default Footer;


